.login-wrapper {
  overflow: hidden;
}
.login-wrapper .login-box {
  width: 320px;
  margin: 200px auto;
}
.login-wrapper .login-box .welcome {
  text-align: center;
}
.login-wrapper .login-box .login-btn {
  width: 100%;
  font-weight: bold;
}
.login-wrapper .login-box .to-register {
  text-align: right;
}
.login-wrapper .login-box .to-register span {
  cursor: pointer;
  transition: 0.3s;
}
.login-wrapper .login-box .to-register span:hover {
  color: #0087f8;
}
