.user-list-item {
  display: flex;
  align-items: center;
  flex-grow: 1;
  width: 100%;
  margin: 12px 0;
  padding: 16px 20px;
  border: 1px solid #e5e9f2;
  border-radius: 3px;
  transition: all 0.3s;
}
.user-list-item:hover {
  border-color: #8500ff;
}
.user-list-item.active {
  background-color: #8500ff;
  border: 1px solid #8500ff;
  color: #fff;
}
.user-list-item.active .chat-time,
.user-list-item.active .text-truncate {
  color: #fff !important;
}
.user-list-item .avatar {
  position: relative;
  width: 48px;
  height: 48px;
  min-width: 48px;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.1);
}
.user-list-item .avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.user-list-item .avatar.avatar-online::before,
.user-list-item .avatar.avatar-offline::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 3px solid #fff;
}
.user-list-item .avatar.avatar-online::before {
  background-color: #44a675;
}
.user-list-item .avatar.avatar-offline::before {
  background-color: #6c757d;
}
.user-list-item .contacts-content {
  padding-left: 14px;
  overflow: hidden;
  flex: 1 1 auto;
}
.user-list-item .contacts-content .contacts-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
}
.user-list-item .contacts-content .contacts-info .chat-name {
  font-weight: bold;
}
.user-list-item .contacts-content .contacts-info .chat-time {
  color: #adb5bd;
}
.user-list-item .contacts-content .contacts-texts {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.user-list-item .contacts-content .contacts-texts .text-truncate {
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #adb5bd;
}
