#root {
  font-family: "Hiragino Sans GB", "Microsoft Ya", Arial, "\5b8b\4f53", "Helvetica Neue", Helvetica, STHeiti, sans-serif;
  font-size: 16px;
  color: #495057;
}
#root .clearfix::after {
  content: '';
  display: table;
  clear: both;
}
#root .hidden-scrollbar {
  scrollbar-width: none;
}
#root .hidden-scrollbar::-webkit-scrollbar {
  display: none;
}
#root ul {
  list-style: none;
}
