.chat-header-wrapper {
  height: 100px;
  padding: 24px;
  background-color: #fff;
  border-bottom: 1px solid #e5e9f2;
}
.chat-header-wrapper .user-info-show {
  display: flex;
  justify-content: space-between;
  line-height: 50px;
  overflow: hidden;
}
.chat-header-wrapper .user-info-show .user-info .user-avatar {
  display: inline-block;
  width: 48px;
  height: 48px;
  margin-right: 10px;
  border-radius: 50%;
  vertical-align: middle;
}
.chat-header-wrapper .user-info-show .user-info .nickname {
  font-size: 18px;
  font-weight: bold;
  vertical-align: middle;
}
.chat-header-wrapper .user-info-show .user-operator .operator-list {
  display: flex;
  flex-wrap: nowrap;
  margin: 0;
}
.chat-header-wrapper .user-info-show .user-operator .operator-list .operator-item {
  cursor: pointer;
  transition: 0.3s;
}
.chat-header-wrapper .user-info-show .user-operator .operator-list .operator-item:hover {
  color: #8500ff;
}
